<template>
	<div id="container">
		<nav>
			<a class="nav-a" href="http://www.utaqi.com/"><img src="./../assets/utaqi-logo.png"></a>
				<ul>
					<li>
						<router-link to="/" exact><span class="iconfont utaqi-icon">&#xe605;</span> <span class="utaqi-title">首页</span></router-link>
						<router-link to="/teachers" exact>讲座</router-link>
						<router-link to="/faq" exact>FAQ</router-link>
						<router-link to="/answer" exact>模拟题答案</router-link>
						<a href="http://www.utaqi.com/mn/"><span class="iconfont">&#xe604;</span></a>
					</li>
				</ul>
		</nav>
	</div>
</template>

<script>
	export default{
		name:"blog-header"
	}
</script>

<style scoped>
	ul{
		list-style-type: none;
		text-align: left;
		padding: 20px;
		display: inline-block;
	}
	li{
		margin: 0 auto;
	}
	a{
		padding: 8px 12px;
		border-radius: 0px;
		text-decoration: none;
		color: #6c798a;
		font-family: 'PingFang SC', 'Helvetica Neue', 'Microsoft YaHei UI', 'Microsoft YaHei', 'Noto Sans CJK SC', Sathu, 'EucrosiaUPC', sans-serif;
		font-size: 16px;
	}
	.nav-a{
		padding: 0;
		text-decoration: none;;
		color: #fff;
		display: none;
	}
	.utaqi-icon{
		display: inline;
		/*font-size: 24px;*/
	}
	.utaqi-title{
		display: none;
	}
	nav{
		background: #fff;
		padding:0;
		margin-bottom: 0;
		box-sizing: border-box;
		height: 60px;
		width: 100%;
		border-bottom: 1px solid #efefef;
	}
	.router-link-active{
		background: rgba(255,255,255,0.1);
		color: #363947;
		font-weight: bold;
	}
	nav img{
		color: #fff;
		padding: 5px;
		float: left;
		height: 60px;
		width: 80px;
		margin-left: 20px;
	}
	.iconfont{
		/*font-size: 20px;*/
		font-family: iconfont;
	}
	*{
		box-sizing: border-box;
	}

	/*宽度大于445px的时候有效*/
	@media (min-width: 445px) {
		.nav-a{
			display: inline;
		}
		.utaqi-icon{
			display: none;
		}
		.utaqi-title{
			display: inline;
		}
	}
	/*宽度小于350px的时候有效*/
	@media (max-width: 465px){
		a{
			font-size: 12px;
		}
	}
</style>


